header {
  padding: 40px 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  top: 0px;
  position: fixed;
  width: 90%;
}

header .logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex: 0.17 1 0%;
      flex: 0.17 1 0%;
  min-width: 190px;
}

header .logo svg {
  width: auto;
  height: 30px;
}

header .logo path {
  stroke-width: 6;
  stroke: #EC2A25;
}

header .buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

@media (max-width: 800px) {
  header .buttons {
    display: none;
  }
}

header .buttons button {
  margin: 0 10px;
  background-color: transparent;
  background-repeat: no-repeat;
  border: '1px solid rgba(255, 255, 255, .3)';
  width: 160px;
  height: 45px;
  border-radius: 22.5px;
  text-transform: uppercase;
  outline: none;
  cursor: pointer;
  -webkit-transition: background-color 0.5s, -webkit-box-shadow 0.5s;
  transition: background-color 0.5s, -webkit-box-shadow 0.5s;
  -o-transition: background-color 0.5s, box-shadow 0.5s;
  transition: background-color 0.5s, box-shadow 0.5s;
  transition: background-color 0.5s, box-shadow 0.5s, -webkit-box-shadow 0.5s;
}

header .buttons button:hover {
  background-color: #EF4230;
  -webkit-box-shadow: 0 10px 10px #1C232B;
          box-shadow: 0 10px 10px #1C232B;
}


